  border-color: $blue;
  color: #fff;
  box-shadow: 0 0 40px 40px $blue inset, 0 0 0 0 $blue;
  transition: all 150ms ease-in-out;
  
 
} */

.topic-btn:hover {
  color: #ffffff !important;
  background: #17252a;
  border-color: #17252a !important;
  transition: all 0.4s ease 0s;
}

.topic-header {
  padding-bottom: 10px;
}

.topic-select {
  margin-bottom: 10px;
}

@media only screen and (max-width: 750px) {
  .topic-box {
    margin-bottom: -30px;
  }
  .topic-header {
    font-size: 20px;
  }

  .topic-select {
    width: 90%;
  }
}
